import { useRouter } from "vue-router";
import { Toast } from 'vant';
import { useStore } from 'vuex';
export default {
  props: ["title", "edit"],
  setup() {
    const store = useStore();
    const router = useRouter();
    const toBack = () => {
      router.back();
    };
    const handleEdit = () => {
      if (store.state.cartList.length) {
        store.commit("edit");
        store.commit('changeDelete');
      } else {
        Toast.fail('请添加商品到购物车！');
      }
    };
    return {
      toBack,
      handleEdit,
      store
    };
  }
};